import React from 'react';
import ReactECharts from 'echarts-for-react';
import dayjs from "dayjs";
import _ from 'lodash';
type RecordItem = {
    tagId: number
    note: string
    category: '+' | '-'
    amount: number
    createdAt:string
}
type Props = {
    value: '-' | '+'
}
const ChartFC: React.FC<Props> = (props) => {
    const records = JSON.parse(window.localStorage.getItem('records') || '[]');
    let Records=records.filter((r:RecordItem)=>r.category===props.value)
    let arr = [];
    for (let i = 0; i < 7; i++) {
        const dateString = dayjs().subtract(i, 'day').format('YYYY-MM-DD');
        let amount=0
        Records.map((r:RecordItem)=>r.createdAt===dateString?amount+=r.amount:'')
        const found = _.find(Records, {createdAt: dateString});
        arr.push({date: dateString, value: found ? amount : 0});
    }
    arr.sort((a,b)=>{
        if(a.date>b.date){
            return 1;
        }else if(a.date<b.date){
            return -1
        }else{
            return 0
        }
    })
    let weekDate:string[] = []
    arr.map(a=>weekDate.push(a.value.toString()))
    let amount=0
    Records.map((r:RecordItem)=>amount+=r.amount)
    let category = props.value==='-'?'支出':'收入'
    const options = {
        title: {
            text: '本周',
            left:"40px",
            top:"40px"
        },
        grid: {
            left: '3%',
            right: '4%',
            bottom: '10%',
            containLabel: true
        },
        xAxis: {
            type: 'category',
            boundaryGap:false,
            data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'],
        },
        yAxis: {
            show: false,
            type: 'value',
        },
        series: [
            {
                data: weekDate,
                type: 'line'
            },
        ],
        tooltip: {
            trigger: 'axis',
        },
    };

    return (
        <>
            <ReactECharts option={options}/>
            <div>本周总{category}:￥{amount}</div>
        </>

    );
};

export default ChartFC;